@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400&display=swap');

.divLanding{
    background-color: #1a0321;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.divLanding main{
    width: 100%;
    height: 95vh;
    display: flex;
    padding-bottom: 10vh;
}


.divLanding main section{
    width: 50%;
    padding: 0px 50px 0 15vh;
    color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.divLanding main aside{
    width: 50%;
    padding: 0px 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}


.divLanding main section h1{
    margin-right: 25vh;
    letter-spacing: 3px;
    font-family: 'Montserrat', sans-serif;
}


.divLanding main section p{
    padding-top: 6vh;
    text-align: left;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 2px;
    font-weight: 100;
    margin-right: 10vh;

}

.CONOCEME{
    color: white;
    text-decoration: none;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: small;
}

.CONOCEME:hover{
    display: inline-block;           /* para que el 'transform scale' funcionen */
    transform: scale(1.2);
}