.StartButton_LinkCreate__1K8ex{
    font-family: consolas;


    position: relative;
    display: inline-block;
    padding: 15px 30px;
    color: rgb(255, 255, 255);
    letter-spacing: 4px;
    font-size: 24px;
    text-decoration: none;
    overflow: hidden;
    transition: 0.2s;
}

.StartButton_LinkCreate__1K8ex:hover{
    background: #a945c7;
    box-shadow: 0 0 10px #a945c7, 0 0 40px #a945c7, 0 0 80px #a945c7;
    transition-delay: 0.5s;
}

.StartButton_LinkCreate__1K8ex span{
    position: absolute;
    display: block;
}

.StartButton_span1__SOy9b{
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg,transparent,#a945c7);
}

