    left: 100%;
    transition: 0.5s;
}

.StartButton_span2__3P_03{
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(180deg,transparent,#a945c7);
}

.StartButton_LinkCreate__1K8ex:hover .StartButton_span2__3P_03{
    top: 100%;
    transition: 0.5s;
    transition-delay: 0.125s;
}


.StartButton_span3__2dUDb{
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg,transparent,#a945c7);
}

.StartButton_LinkCreate__1K8ex:hover .StartButton_span3__2dUDb{
