#divForm{
    width: 100vw;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    overflow: hidden;
}

#divForm form{
    background-color: rgb(26, 3, 33,0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 5vh 8vh;
    width: 700px;
    border-radius: 30px;
}

#divForm form > divs {
    width: 100%;
}

#divForm form >divs> div{
    width: 100%;
    /* background-color: red; */
    display: flex;
    justify-content: space-between;
    margin: 5vh 0;
    /* padding: 10px; */
    /* flex-direction: column; */
}


#divForm form h1{
    margin: 1.5vh 0 2.5vh 0;
}




.divsSmallImputs input{
    font-size: 15px;
    padding: 5px;
    width: 65%;
    border-radius: 6px;
}
.divsSmallImputs input::placeholder{
    font-size: 14px;
}

.bigTextContainer{
    flex-direction: column;
}

.textarea {
    border-radius: 6px;
    padding: 8px 5px;
    width: 100%;
    display: flex;
    font-size: 16.5px;
    height: 8vh;
}

.textarea::placeholder{
    font-size: 15px;
}

#steps{
    margin-top: 3.5vh;
}
#steps textarea{
    height: 10vh;
}

#dietsOptions{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 3.5vh;
    width: 100%;
}

#sendButton{
    padding: 1vh;
}







