.card{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    /* letter-spacing: 0.03em; */
    line-height: 1.6;

    /* lo de arrriba ese de *{} */

    text-decoration: none;
    width: 330px;
    height: 350px;
    border-radius: 8px;
    box-shadow: 0 2px 2px rgba(0,0,0,0.2);
    overflow: hidden;
    margin: 20px;
    transition: all 0.25s;
}


.card:hover{
    transform: translateY(-15px);
    box-shadow: 0 12px 16px rgba(0,0,0,0,2);
}

.card img{
    width: 330px;
    height: 220px;
}

.card h4{
    letter-spacing: 0.04em;
    font-weight: 600;
    height: 50px;
    color: #1a0321;
    margin: 5px;
    font-size: 15.5px;
    text-align: center;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    /* text-decoration:none !important;               INVESTIGAR POR QUÉ LA ETIQUETA LINK PISA ESTÁ PROPIEDAD                           */
}

.card p{
    font-size: 12px;
    /* font-weight: 100;       agregar otra fuente para ponerle weight*/
    color: rgb(255, 255, 255);
    text-align: start; 
}

.card strong{
    font-size: 13px;
}

.card hr{
    color: #1a0321;
}

.card span{
    display: flex;
    align-items: center;
    justify-content: start;
    height: 62px;
    background-color: #1a0321;
    padding: 0 1rem;
}

