.formSearchBar{
    width: 100%;
    max-width: 700px;
    background: rgba(255, 255, 255, 0.2);
    display: flex;
    align-items: center;
    border-radius: 60px;
    /* backdrop-filter: blur(4px) saturate(180%); */
}

.formSearchBar input{
    width: 500px;
    background: transparent;
    flex: 1;
    border: 0;
    outline: none;
    padding: 10px 0 10px 20px;
    font-size: 20px;
    color: #cac7ff;

}

.formSearchBar input::placeholder{
    color: rgba(202, 199, 255, 0.5);
    height: 100px;
    font-size: 19px;
}

.formSearchBar button img{
    width: 22px;
}

.formSearchBar button{
    border: 0;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    background: rgb(202, 199, 255, 0.2);
    cursor: pointer;
}