.LinkCreate{
    font-family: consolas;


    position: relative;
    display: inline-block;
    padding: 15px 30px;
    color: rgb(255, 255, 255);
    letter-spacing: 4px;
    font-size: 24px;
    text-decoration: none;
    overflow: hidden;
    transition: 0.2s;
}

.LinkCreate:hover{
    background: #a945c7;
    box-shadow: 0 0 10px #a945c7, 0 0 40px #a945c7, 0 0 80px #a945c7;
    transition-delay: 0.5s;
}

.LinkCreate span{
    position: absolute;
    display: block;
}

.span1{
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg,transparent,#a945c7);
}

.LinkCreate:hover .span1{
    left: 100%;
    transition: 0.5s;
}

.span2{
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(180deg,transparent,#a945c7);
}

.LinkCreate:hover .span2{
    top: 100%;
    transition: 0.5s;
    transition-delay: 0.125s;
}


.span3{
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg,transparent,#a945c7);
}

.LinkCreate:hover .span3{
    right: 100%;
    transition: 0.5s;
    transition-delay: 0.125s;
}

.span4{
    bottom: -100%;
    left: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(360deg,transparent,#a945c7);
}

.LinkCreate:hover .span4{
    bottom: 100%;
    transition: 0.5s;
    transition-delay: 0.375s;
}





