@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;800&display=swap');


.header{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Nunito', sans-serif;
    font-size: 18px;
  /* aca arriba está el código que deberái estar en *{} */

    width: 100%;
    background-color: #1a0321;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
    padding: 5px 10%;
}

.header nav{
    display: flex;
    align-items: center;
    justify-content: center;
}

.back{
    width: auto;
    cursor: pointer;
    text-decoration: none;
    transition: all 0.3s;
    color: #eceff1;
}

.back:hover{
    transform: scale(1.2);
}

.header ul {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
}


.header  li{
    display: inline-block;
    padding: 0 20px;
    width: 300px;
}

.link{
    text-decoration: none;
    color: #eceff1;
    transition: all 0.3s;
    font-size: 17px;
    width: auto;
    transition: all 0.3s;
    display: inline-block;
}

.link:hover{
    transform: scale(1.05);
    color: yellow;
}

.create{
    font-weight: 700;
    width: 125px;
    height: 35px;
    color: yellow;
    text-decoration: none;
    background-color: #a945c7;
    border-radius: 50px;
    cursor: pointer;
    transition: all 0.3s;
    /* margin-bottom: 13px; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.create:hover{
    background-color: white;
    color: black;
    transform: scale(1.1);
}

